import { renderCollection } from './render-collection';
import api from './api/api-service';
import NProgress from 'nprogress';
import 'nprogress/nprogress.css';
import { errorModal } from './components/notify';

import Pagination from 'tui-pagination';
import 'tui-pagination/dist/tui-pagination.css';
const paginationRef = document.getElementById('pagination1');
import { gallery } from './references/refs';

document.addEventListener('DOMContentLoaded', startPage);

paginationRef.addEventListener('click', event => {
  console.dir(event.target);

  if (
    event.target.className !== 'tui-page-btn') {
    return;
  }

  gallery.innerHTML = '';

  api.setPage(event.target.innerText);  

  startPage();
});

// Функция отрисовки трендов на главной
async function startPage() {
  NProgress.start();

  // api.setPage(33);

  try {
    const data = await api.getFullTrendData();

    const movies = data.fullTrendData;
    const currentPage = data.currentPage;
    // const totalPages = data.totalPages;
    const totalResults = data.totalResults;

    const instance = new Pagination(paginationRef, {
      totalItems: totalResults,
      itemsPerPage: 20,
      visiblePages: 5,
      page: api.page,
      centerAlign: true,
    });

    // instance.getCurrentPage(currentPage);
    instance.movePageTo(currentPage);

    renderCollection(movies);
  } catch (error) {
    errorModal();
    console.error('Smth wrong with start page fetch' + error);
  }

  NProgress.done();
}
